import { asort_by_width } from "universal-common-scripts/functions/asort_words";
import { asort_by_length_and_position, asort_by_matches_in_list } from "universal-common-scripts/functions/asort_strings";
import add_abbreviated_list from "universal-wordsalad-scripts/suggestions/function/add_abbreviated_list.js";
import phrase_remove_synonym from "universal-wordsalad-scripts/suggestions/function/phrase_remove_synonym.js";
import phrase_remove_last_pre from "universal-wordsalad-scripts/suggestions/function/phrase_remove_last_pre.js";
import * as sh from "universal-common-scripts/functions/sh.js";

/**
 * TODO:
 * "coffee.top", "coffee.best", "coffee. - find main word of original phrase, instead of just limiting right-left
 * get synonyms of that word, add it to next list
 *
 */

/**
 * Lists
 * @param params {object}
 * @param params.chunks_keys {array} - array of key strings
 * @param params.chunks_dict {object} - object of key : {key:'',root:'',derivations:[],ok_list:[],...etc}
 * @returns this {object}
 * @returns slists {array} - lists of lists of phrases
 * @returns this.etc {any} - whatever else was in this
 */
export default function make_tlds({ chunks_keys, chunks_dict }) {

  /*
   *
   * VALIDATE
   *
   */
  let words_keys = chunks_keys.filter(key => !key.includes(' '));


  /*
   *
   * Add TLDs to Suggestions Phrases
   *
   */
  let slists = {};
  slists["phrases"] = [];
  let num_tlds = this.tlds.length;
  let num_phrases = this.wip_phrases.length;
  this.wip_phrases = asort_by_length_and_position(this.wip_phrases, 5);
  this.wip_phrases.forEach((words, pi) => {
    // add a number of tlds to this phrase
    let num_add = Math.ceil(((num_phrases - pi) / num_phrases) * num_tlds);
    // add ${i} tld to phrase
    for (let i = 0; i < num_add; i++) {
      // make phrase, add it
      let phrase = {};
      phrase.words = words;
      phrase.tld = this.tlds[i];
      phrase.string = phrase.words.join(' ') + ' .' + phrase.tld;
      // phrase.string = phrase.words.join(' ') + ' .' + phrase.tld;
      slists["phrases"].push(phrase);
    }
  });


  /*
   *
   * Edit / Remove / Add - phrases in each list
   *
   */
  this.lists = {}; // permanent lists -- write to
  let added_phrases = {};
  for (let listname in slists) {
    /*
     * Prepare list
     */
    this.lists[listname] = []; // new list -- write to
    let list = slists[listname]; // old list -- read from
    /*
     * Iterate each phrase in list
     */
    let li = 0;
    while (li < list.length) {
      let phrase = list[li];
      phrase.string = phrase.words.join(' ') + ' .' + phrase.tld;
      /*
       * Remove phrase - if duplicate
       */
      if (added_phrases[phrase.string]) {
        li++;
        continue;
      }
      /*
       * Edit phrase - remove words which are redundant
       */
      phrase = phrase_remove_synonym(phrase, chunks_dict);
      phrase = phrase_remove_last_pre(phrase, chunks_dict); // remove trailing conjunctions/determiners/etc
      phrase = phrase_remove_last_pre(phrase, chunks_dict); // again, same thing, in case there are 2 trailing
      /*
       * Remove phrase - if modification removed all its words
       */
      if (phrase.words.length === 0) {
        li++;
        continue;
      }
      /*
       * Fix string - after editing
       */
      phrase.string = phrase.words.join(' ') + ' .' + phrase.tld;
      /*
       * Add phrase
       */
      for (let word of phrase.words) {
        if (chunks_keys.includes(word)) {
          let row = chunks_dict[word];
          /*
           * Try .root/.singular/.acronym/.abbreviation
           */
          if (row.root || row.singular || row.acronym || row.abbreviation) {
            // find shorter word
            let alt_word = row.singular || row.acronym || row.abbreviation;
            // if shorter
            if (alt_word && alt_word.length < word.length) {
              // replace word
              let idx_word = phrase.words.indexOf(word);
              let phrase2 = {
                words: [...phrase.words],
                tld: phrase.tld,
              };
              // replace word with alt
              phrase2.words.splice(idx_word, 1, alt_word);
              // add new phrase
              // to end of current list,
              // so it will be modified later,
              // and checked for uniqueness later also
              list.push(phrase2);
            }
          }
        }
      }
      /*
       * Save phrase - as string
       */
      this.lists[listname].push(phrase.string); // add to list
      added_phrases[phrase.string] = true; // add to unique list
      /*
       * Next phrase
       */
      li++;
    }
  }

  /*
   * Sort by length/position
   */
  this.lists['phrases'] = this.lists['phrases'];

  /*
   * Add list of aggregated abbreviated phrases
   */
  this.lists = add_abbreviated_list(this.lists, chunks_keys, chunks_dict);


  /*
   *
   * RETURN
   *
   */
  return this;
};
