import techterms from "../../data/words/tech.js";
import tlds_preferences from "universal-wordsalad-scripts/data/domains/sort_preference.js";
import tlds_tech from "universal-wordsalad-scripts/data/domains/tech.js";
import domains_from_chunks from "universal-wordsalad-scripts/suggestions/function/domains_from_chunks";
import { asort_by_rating_and_position, asort_by_length_and_position } from "universal-common-scripts/functions/asort_strings";
import { arr_diff } from "universal-common-scripts/functions/arrays";
import * as sh from "universal-common-scripts/functions/sh.js";

function rate_pos1(pos1) {
  if (pos1 === 'nouns') return 10;
  if (pos1 === 'adjectives' || pos1 === 'verbs' || pos1 === 'adverbs') return 4;
  return 1;
}

function sort_tld_keys_by_domlength_asc(b, a) {
  let domlength = this; // usage: arr.sort(func.bind(obj))
  return (domlength[b] || 0) - (domlength[a] || 0);
}

function sort_tld_keys_by_posrating_desc(b, a) {
  let posrating = this; // usage: arr.sort(func.bind(obj))
  return (posrating[a] || 0) - (posrating[b] || 0);
}

/**
 * Suggestions
 * @param params {object}
 * @param params.chunks_keys {array} - array of key strings
 * @param params.chunks_dict {object} - object of key : {key:'',root:'',derivations:[],ok_list:[],...etc}
 * @returns this {object}
 * @returns this.tlds {array} - list of domain extensions
 */
export default function make_tlds({ chunks_keys, chunks_dict, is_tech_query }) {

  /*
   * .TLDS
   */
  //
  // promote keys with more important POS (nouns)
  // promote keys with fewer domains
  let tld_doms_max = 0;
  let tld_keys_posrating = {}; // { key: num_posrating, }
  let tld_keys_domlength = {}; // { key: num_domlength, }
  for (let key of chunks_keys) {
    let row = chunks_dict[key];
    if (row.poss) {
      // for rating, remember max domains
      let num_doms = (row.poss.domains && row.poss.domains.length) || 0;
      if (num_doms > tld_doms_max) {
        tld_doms_max = num_doms;
      }
      // rate
      tld_keys_domlength[key] = num_doms;
      tld_keys_posrating[key] = rate_pos1(row.pos1);
    }
  }
  // sort by ratings
  let tld_keys = [...chunks_keys];
  tld_keys.sort(sort_tld_keys_by_domlength_asc.bind(tld_keys_domlength)); // first, sort by less important
  // tld_keys.sort(sort_tld_keys_by_posrating_desc.bind(tld_keys_posrating)); // finally, sort by most important rating

  //
  // generate tlds
  let tlds_add = domains_from_chunks(tld_keys, chunks_dict);

  //
  // promote tech tlds
  // let tlds_tech = [];
  // if (is_tech_query) {
  // 	let i = 0;
  // 	while (i < arr_tlds.length) { // new length read and compared to i each time
  // 		let tld = arr_tlds[i];
  // 		// sort tld into correct list
  // 		if (techterms[tld] && tld !== 'bot') {
  // 			tlds_tech.push(tld);
  // 		}
  // 		i++; // if length did not change, then increment index like normal
  // 	}
  // }
  //
  // tlds suggestions
  // will be added to user's checked/unchecked request data
  // let tlds_add = [...new Set([...tlds_tech, ...arr_tlds])].slice(0, 15);

  //
  // promote tlds which match key
  let i = 0;
  while (i < tlds_add.length) { // new length read and compared to i each time
    let tld = tlds_add[i];
    let promote = false;
    // promote to front of list ?
    for (let key of chunks_keys) {
      let chunk = chunks_dict[key];
      if (
        chunk.derivations && chunk.derivations.includes(tld) ||
        chunk.plural && chunk.plural.includes(tld)
      ) {
        promote = true;
      }
    }
    if (promote) {
      let str = tlds_add.splice(i, 1);
      if (str.length) {
        tlds_add.unshift(str[0]);
      }
    }
    i++; // if length did not change, then increment index like normal
  }
  //
  // promote tlds which match key
  i = 0;
  while (i < tlds_add.length) { // new length read and compared to i each time
    let tld = tlds_add[i];
    let promote = false;
    // promote to front of list ?
    for (let key of chunks_keys) {
      let chunk = chunks_dict[key];
      if (
        chunk.key.includes(tld) ||
        chunk.root && chunk.root.includes(tld)
      ) {
        promote = true;
      }
    }
    if (promote) {
      let str = tlds_add.splice(i, 1);
      if (str.length) {
        tlds_add.unshift(str[0]);
      }
    }
    i++; // if length did not change, then increment index like normal
  }

  /*
   *
   * MANUALLY APPEND tech tlds
   *
   */
  if (tlds_add.length < 15) {
    let tlds_add_tech = [];
    if (is_tech_query) {
      tlds_add_tech = tlds_tech;
    }
    tlds_add = [...new Set([
      ...tlds_add,
      ...tlds_add_tech
    ])];
  } else {
    tlds_add = [...new Set(tlds_add)];
  }

  /*
   *
   * SORT
   *
   */
  tlds_add = asort_by_rating_and_position(tlds_add, tlds_preferences, 25);
  tlds_add = asort_by_length_and_position(tlds_add, 25);

  /*
   *
   * MAGIC
   *
   */
  if (chunks_keys.includes('airlines')) {
    if (tlds_add.includes('design')) {
      tlds_add.splice(tlds_add.indexOf('design'), 1);
    }
  }

  //
  // add to (user-chosen) tlds
  // sh.log(sh.jsp(arr_tlds));
  // sh.log(sh.jsp(tlds_add));
  // sh.log(sh.jsp(this.tlds));
  // sh.log(sh.jsp(this.tlds_unchecked));
  this.tlds = [...new Set([...this.tlds_user, 'com', ...tlds_add, ...this.tlds])];
  this.tlds = arr_diff(this.tlds, this.tlds_unchecked);

  /*
   *
   * RETURN
   *
   */
  return this;
};
